.o-editor {

    .basicField {
        position: relative;
        width: 100%;

        &.has-error {

            &::before{
                content: "";
                background: rgb(220 50 50 / 50%);
                width: $o-space-10;
                height: 100%;
                position: absolute;
                top: 0;
                left: -$o-space-70;
            }

            &:has( > .components-panel ) {

                &::before {
                    content: none;
                }
                & > .components-panel {
                    border-color: rgba(220, 50, 50, 0.5) !important;
                }
            }
        }
        &.has-warning {

            &::before{
                content: "";
                background: rgb(255 105 0 / 50%) !important;
                width: $o-space-10;
                height: 100%;
                position: absolute;
                top: 0;
                left: -$o-space-70;
            }

            &:has( > .components-panel ) {

                &::before {
                    content: none;
                }
                & > .components-panel {
                    border-color: rgb(255 105 0 / 50%) !important;
                }
            }
        }

        .error, .warning {
            background: none;
            border: none;
            padding: 0;
            margin: 0 0 0 $o-space-30;
            box-shadow: none;
            display: flex;
            align-items: center;
            font-weight: 400;
            font-size: 13px;

            .dashicon {
                margin-right: $o-space-10;
            }

            ul {
                margin: 0;

                li {
                    margin: 0;
                }
            }
        }
        .error {
            color: #d63638;
        }
        .warning {
            color: var(--wp--preset--color--luminous-vivid-orange);
        }

        .responsive-tab-panel-wpe-component {
            position: relative;
            width: 100%;
            padding-top: $o-space-20;
        
            .responsive-buttons-container {
                position: absolute;
                z-index: 1;
                top: 0;
                right: 0;
            }
        
            .tab-panel-wpe-component.videoType {
                margin-top: $o-space-40;
        
                .components-tab-panel__tabs {
                    position: relative;
                    z-index: 1;
                    background: $background300;
                }
            }
        
        }

        .default-overlay-container {
            position: absolute;
            top: -5px;
            left: -10px;
            width: calc(100% + 20px);
            height: calc(100% + 10px);
            background: rgb(252 252 252 / 80%);
            display: flex;
            justify-content: center;
            align-items: center;
            z-index: 1;

            .components-button {
                opacity: 0;
                box-shadow: 2px 2px 10px rgb(0 0 0 / 30%) !important;
                transition: $o-transition-default;
            }

            &:hover {

                .components-button {
                    opacity: 1;
                }
            }
        }
    }
}